import { Container, Box, Backdrop, TextField } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../app/store';
import { useState } from 'react';
import { hideCaptcha } from '../features/auth/authSlice';
interface ICaptchaProps {
    onsubmit(code: number): void;
}

export const Captcha = ({ onsubmit }: ICaptchaProps) => {
    const dispatch = useDispatch();
    const { captcha } = useSelector((state: RootState) => state.auth);
    const [codeInput, setCodeInput] = useState('');

    const handleTextFieldChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const code = e.currentTarget.value;
        if (code.length < 5) setCodeInput(code);

        if (code.length === 4) {
            onsubmit(+code);
            setCodeInput('');
        }
    };

    return (
        <Backdrop
            sx={{ zIndex: 1 }}
            open
            onClick={e => {
                if (e.target === e.currentTarget) dispatch(hideCaptcha());
            }}
        >
            <Container sx={{ textAlign: 'center' }}>
                <Box
                    component="form"
                    sx={{
                        backgroundColor: '#fff',
                        p: 1,
                        borderRadius: 2,
                        textAlign: 'center',
                        display: 'inline-block',
                    }}
                >
                    <img
                        src={'data:image/jpeg;base64,' + captcha.b64code}
                        alt="Loading..."
                        style={{ margin: '0 auto' }}
                    />
                    <TextField
                        label="Code"
                        variant="outlined"
                        size="small"
                        autoFocus
                        value={codeInput}
                        type="number"
                        onChange={handleTextFieldChange}
                        autoComplete="off"
                    />
                </Box>
            </Container>
        </Backdrop>
    );
};
