import { ErrorPage } from './routes/ErrorPage';
import { createHashRouter, RouterProvider } from 'react-router-dom';
import { Layout } from './components/Layout';
import { MeetsPage } from './routes/MeetsPage';
import { CssBaseline } from '@mui/material';
import { MeetPage } from './routes/MeetPage';
import { ChatsPage } from './routes/ChatsPage';
import { ChatPage } from './routes/ChatPage';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from './app/store';
import { AuthPage } from './routes/AuthPage';
import { SendPhotoPage } from './routes/SendPhotoPage';
import { useEffect } from 'react';
import { getUserPhotos } from './features/api/chat';
import { requestUserPhotos } from './features/chat/chatSlice';

const authorizedRouter = createHashRouter([
    {
        path: '/',
        element: <Layout />,
        errorElement: <ErrorPage />,
        children: [
            {
                index: true,
                element: <ChatsPage />,
                errorElement: <ErrorPage />,
            },
            {
                path: 'initial',
                element: <MeetsPage />,
                errorElement: <ErrorPage />,
                children: [
                    {
                        path: ':interlocutorId',
                        element: <MeetPage />,
                    },
                ],
            },
        ],
    },
    {
        path: 'chat/:interlocutorId',
        element: <ChatPage />,
        errorElement: <ErrorPage />,
    },
    {
        path: 'chat/:interlocutorId/sendPhoto',
        element: <SendPhotoPage />,
        errorElement: <ErrorPage />,
    },
]);

const unauthorizedRouter = createHashRouter([
    {
        path: '*',
        element: <AuthPage />,
        errorElement: <ErrorPage />,
    },
]);

export const App = () => {
    const { isAuthorized } = useSelector((state: RootState) => state.auth);
    const dispatch = useDispatch();
    const router = isAuthorized ? authorizedRouter : unauthorizedRouter;

    useEffect(() => {
        dispatch(requestUserPhotos());
    }, [dispatch]);

    return (
        <>
            <CssBaseline />
            <RouterProvider router={router} />
        </>
    );
};
