import { Container, Box, Backdrop, TextField, Button } from '@mui/material';
import htmlParser from 'html-react-parser';
import { UserHeadline } from '../components/UserHeadline';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../app/store';
import { Send as SendIcon } from '@mui/icons-material';
import { Captcha } from '../components/Captcha';
import { useState, useEffect } from 'react';
import { resetIsMessageSended, sendMessage } from '../features/chat/chatSlice';

export const MeetPage = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { selectedMeet } = useSelector((state: RootState) => state.meets);
    const {
        captcha: { isShown: isCaptchaShown },
    } = useSelector((state: RootState) => state.auth);
    const { isMessageSended } = useSelector((state: RootState) => state.chat);
    const [message, setMessage] = useState('');

    const handleTextFieldChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setMessage(e.currentTarget.value);
    };

    const handleSubmit = (e: React.ChangeEvent<HTMLFormElement>) => {
        e.preventDefault();
        dispatch(
            sendMessage({ type: 'text', data: message, recipientId: human.id })
        );
    };

    useEffect(() => {
        if (!isMessageSended) return;
        navigate(-1);
        dispatch(resetIsMessageSended());
    }, [dispatch, navigate, isMessageSended]);

    if (!selectedMeet) return <></>;
    const { human, dialog } = selectedMeet;
    return (
        <>
            <Backdrop
                sx={{
                    zIndex: 1,
                    alignItems: 'flex-end',
                }}
                open
                onClick={e => {
                    if (e.target === e.currentTarget) navigate(-1);
                }}
            >
                <Container
                    sx={{
                        backgroundColor: '#fff',
                        py: 1,
                        borderRadius: 3,
                        borderBottomLeftRadius: 0,
                        borderBottomRightRadius: 0,
                    }}
                >
                    <UserHeadline
                        name={human.name}
                        age={human.age}
                        city={human.city}
                    />
                    <Box color="text.secondary" mb={1}>
                        {htmlParser(dialog.message)}
                    </Box>
                    <Box
                        display="flex"
                        alignItems="stretch"
                        component="form"
                        onSubmit={handleSubmit}
                    >
                        <TextField
                            label="Message"
                            variant="outlined"
                            size="small"
                            value={message}
                            onChange={handleTextFieldChange}
                            autoComplete="off"
                            sx={{ flexGrow: 1 }}
                        />
                        <Button
                            type="submit"
                            disabled={!message.length}
                            sx={{
                                display: 'flex',
                                alignItems: 'center',
                                cursor: 'pointer',
                            }}
                        >
                            <SendIcon />
                        </Button>
                    </Box>
                </Container>
            </Backdrop>
            {isCaptchaShown && (
                <Captcha
                    onsubmit={(code: number) => {
                        dispatch(
                            sendMessage({
                                type: 'text',
                                data: message,
                                recipientId: human.id,
                                code,
                            })
                        );
                    }}
                />
            )}
        </>
    );
};
