import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';

export interface IMessage {
    id: number;
    fromId: number;
    message: string;
    added: Date;
    isImg: boolean;
    src?: string;
}

export interface IImage {
    id: string;
    src: string;
    alt: string;
    sender: number;
}

export interface IChatState {
    messages: IMessage[];
    isMessageSended: boolean;
    userPhotos: IImage[];
}

const initialState: IChatState = {
    messages: [],
    isMessageSended: false,
    userPhotos: [],
};

export const chatSlice = createSlice({
    name: 'chat',
    initialState,
    reducers: {
        sendMessage: (
            _state,
            _action: PayloadAction<{
                type: 'photo' | 'text';
                data: string;
                recipientId: number;
                code?: number;
            }>
        ) => {},
        sendMessageSuccess: state => {
            state.isMessageSended = true;
        },
        resetIsMessageSended: state => {
            state.isMessageSended = false;
        },
        setUserPhotos: (state, action: PayloadAction<IImage[]>) => {
            state.userPhotos = action.payload;
        },
        setMessages: (state, action: PayloadAction<IMessage[]>) => {
            state.messages = action.payload;
        },
        addMessage: (state, action: PayloadAction<IMessage>) => {
            state.messages.unshift(action.payload);
        },
        requestMessages: (_state, _action: PayloadAction<number>) => {},
        requestUserPhotos: () => {},
        requestUploadPhoto: (_state, _action: PayloadAction<File>) => {},
    },
});

export const {
    sendMessage,
    setMessages,
    setUserPhotos,
    addMessage,
    requestMessages,
    sendMessageSuccess,
    resetIsMessageSended,
    requestUserPhotos,
    requestUploadPhoto,
} = chatSlice.actions;
export const chatReducer = chatSlice.reducer;
