import { Link, Outlet, useLocation } from 'react-router-dom';
import {
    BottomNavigation,
    BottomNavigationAction,
    Box,
    Container,
    createTheme,
    ThemeProvider,
    bottomNavigationActionClasses,
    Toolbar,
    Typography,
    AppBar,
} from '@mui/material';
import {
    Email as EmailIcon,
    Favorite as FavoriteIcon,
} from '@mui/icons-material';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../app/store';
import { logout } from '../features/auth/authSlice';

const theme = createTheme({
    components: {
        MuiBottomNavigationAction: {
            styleOverrides: {
                root: ({ ownerState }) => ({
                    [`&.${bottomNavigationActionClasses.selected}`]: {
                        ...(ownerState.label === 'Dating' && { color: '#f33' }),
                    },
                }),
            },
        },
    },
});

export const Layout = () => {
    const location = useLocation();
    const dispatch = useDispatch();
    const { isAuthorized } = useSelector((state: RootState) => state.auth);
    return (
        <ThemeProvider theme={theme}>
            <AppBar position="static" sx={{ mb: 2 }}>
                <Container>
                    <Toolbar disableGutters>
                        <Typography
                            variant="h6"
                            component="div"
                            sx={{ flexGrow: 1 }}
                        >
                            Freedom Chat
                        </Typography>
                        {isAuthorized && (
                            <Box
                                sx={{ cursor: 'pointer' }}
                                onClick={() => {
                                    dispatch(logout());
                                }}
                            >
                                X
                            </Box>
                        )}
                    </Toolbar>
                </Container>
            </AppBar>
            <Box
                sx={{
                    paddingBottom: '60px',
                }}
            >
                <Container>
                    <Outlet />
                </Container>
            </Box>
            <BottomNavigation
                value={location.pathname.slice(1).split('/').at(0)}
                showLabels
                sx={{
                    position: 'fixed',
                    bottom: 0,
                    left: 0,
                    width: '100%',
                }}
            >
                <BottomNavigationAction
                    value={''}
                    label="Messages"
                    icon={<EmailIcon />}
                    component={Link}
                    to=""
                />
                <BottomNavigationAction
                    value={'initial'}
                    label="Dating"
                    icon={<FavoriteIcon />}
                    component={Link}
                    to="initial"
                />
            </BottomNavigation>
        </ThemeProvider>
    );
};
