import { useEffect } from 'react';
import { Typography, List } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { requestMeets, selectMeet } from '../features/meets/meetsSlice';
import { RootState } from '../app/store';
import { Chat } from '../components/Chat';
import { Outlet } from 'react-router-dom';

export const MeetsPage = () => {
    const dispatch = useDispatch();
    const { meets } = useSelector((state: RootState) => state.meets);

    useEffect(() => {
        dispatch(requestMeets());

        const updateMeetsIntervalId = setInterval(() => {
            dispatch(requestMeets());
        }, 20000);

        return () => {
            clearInterval(updateMeetsIntervalId);
        };
    }, [dispatch]);
    return (
        <>
            <Typography variant="h4" align="center">
                Meets
            </Typography>
            <List>
                {meets.map((meet, idx) => (
                    <Chat
                        {...meet}
                        key={meet.id}
                        onclick={() => dispatch(selectMeet(idx))}
                        to={meet.human.id + ''}
                    />
                ))}
                {!meets.length && (
                    <Typography variant="h5" align="center">
                        No meets
                    </Typography>
                )}
            </List>
            <Outlet />
        </>
    );
};
