import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';

export interface IAuthState {
    refreshToken: string;
    accessToken: string;
    userId: number;
    captcha: {
        token: string;
        b64code: string;
        isShown: boolean;
    };
    isAuthorized: boolean;
    credentialsBuffer: {
        userId: number;
        password: string;
    };
}

const refreshToken = localStorage.getItem('refreshToken') ?? '';
const userId = +(localStorage.getItem('userId') ?? '');

const initialState: IAuthState = {
    accessToken: localStorage.getItem('accessToken') ?? '',
    refreshToken,
    userId,
    captcha: {
        token: '',
        b64code: '',
        isShown: false,
    },
    isAuthorized: !!(refreshToken && userId),
    credentialsBuffer: {
        userId: 0,
        password: '',
    },
};

export const authSlice = createSlice({
    name: 'auth',
    initialState,
    reducers: {
        setAccessToken: (state, action: PayloadAction<string>) => {
            state.accessToken = action.payload;
        },
        requestLoginSuccess: (
            state,
            action: PayloadAction<{
                refreshToken: string;
                accessToken: string;
                userId: number;
            }>
        ) => {
            state.refreshToken = action.payload.refreshToken;
            state.accessToken = action.payload.accessToken;
            state.userId = action.payload.userId;
            state.isAuthorized = true;
        },
        hideCaptcha: state => {
            state.captcha.isShown = false;
        },
        setCaptcha: (
            state,
            action: PayloadAction<{ token: string; b64code: string }>
        ) => {
            state.captcha = {
                ...state.captcha,
                ...action.payload,
            };
        },
        requestLogin: (
            state,
            action: PayloadAction<{
                userId: number;
                password: string;
                code?: number;
            }>
        ) => {
            state.credentialsBuffer = action.payload;
        },
        logout: state => {
            state.isAuthorized = false;
        },
        requestAccessToken: () => {},
        requestCaptcha: state => {
            state.captcha.isShown = true;
            state.captcha.b64code = '';
        },
    },
});

export const {
    setAccessToken,
    requestLoginSuccess,
    requestLogin,
    logout,
    requestAccessToken,
    requestCaptcha,
    hideCaptcha,
    setCaptcha,
} = authSlice.actions;
export const authReducer = authSlice.reducer;
