import {
    getCapthcaCode,
    getCapthcaToken,
    getCapthcaId,
    postLogin,
    getAccessToken,
} from './../api/auth';
import { RootState } from './../../app/store';
import {
    requestAccessToken,
    requestCaptcha,
    requestLogin,
    requestLoginSuccess,
    logout,
    setAccessToken,
    setCaptcha,
} from './../auth/authSlice';
import { call, put, select, takeLeading, takeLatest } from 'redux-saga/effects';

function* getAccessTokenWorker(): Generator<any, any, any> {
    try {
        const { refreshToken } = yield select((state: RootState) => state.auth);
        const res = yield call(getAccessToken, refreshToken);
        const accessToken: string = res.data.token;

        localStorage.setItem('accessToken', accessToken);
        yield put(setAccessToken(accessToken));
    } catch (error: any) {
        if (error.response && error.response.status === 401) {
            yield put(requestAccessToken());
        } else console.log(error);
    }
}

function* getCaptchaWorker(): Generator<any, any, any> {
    try {
        const { accessToken } = yield select((state: RootState) => state.auth);
        const {
            data: { id: captchaId },
        } = yield call(getCapthcaId, accessToken);

        let captchaToken;
        do {
            ({
                data: { token: captchaToken },
            } = yield call(getCapthcaToken, captchaId));
        } while (!captchaToken);
        const res = yield call(getCapthcaCode, captchaToken);
        yield put(setCaptcha({ token: captchaToken, b64code: res.data.image }));
    } catch (error) {
        console.log(error);
    }
}

interface ILoginWorkerArgs {
    type: string;
    payload: {
        userId: number;
        password: string;
        code?: string;
    };
}

function* loginWorker({ payload }: ILoginWorkerArgs): Generator<any, any, any> {
    try {
        const { captcha } = yield select((state: RootState) => state.auth);
        const { data } = yield call(postLogin, {
            ...payload,
            captchaToken: captcha.token,
            code: payload.code,
        });
        switch (data.error) {
            case 'auth':
                alert(data.say);
                break;
            case 'code':
                yield put(requestCaptcha());
                break;
            default:
                yield put(
                    requestLoginSuccess({
                        refreshToken: data.refresh,
                        accessToken: data.token,
                        userId: payload.userId,
                    })
                );
                localStorage.setItem('refreshToken', data.refresh);
                localStorage.setItem('accessToken', data.token);
                localStorage.setItem('userId', payload.userId + '');
        }
    } catch (error) {
        console.log(error);
    }
}

function* logoutWorker(): Generator<any, any, any> {
    yield localStorage.removeItem('userId');
    yield localStorage.removeItem('refreshToken');
}

export function* authWatcher() {
    yield takeLeading(requestAccessToken.type, getAccessTokenWorker);
    yield takeLeading(requestCaptcha.type, getCaptchaWorker);
    yield takeLatest(requestLogin.type, loginWorker);
    yield takeLatest(logout.type, logoutWorker);
}
