import {
    Box,
    Button,
    ButtonBase,
    Container,
    ImageList,
    ImageListItem,
    Input,
    Grid,
    ListItem,
    ListItemText,
    OutlinedInput,
    TextField,
} from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { Captcha } from '../components/Captcha';
import {
    requestMessages,
    resetIsMessageSended,
    sendMessage,
    requestUploadPhoto,
} from '../features/chat/chatSlice';
import { useEffect } from 'react';
import { Navigate, useNavigate, useParams } from 'react-router-dom';
import { RootState } from '../app/store';
import htmlParser from 'html-react-parser';
import { IMG_DOMEN } from '../app/config';
import {
    Send as SendIcon,
    CameraAlt as CameraAltIcon,
} from '@mui/icons-material';
import { useState } from 'react';
import axios from 'axios';

export const SendPhotoPage = () => {
    const params = useParams();
    const interlocutorId = +(params.interlocutorId ?? '');
    const dispatch = useDispatch();
    const { userPhotos } = useSelector((state: RootState) => state.chat);
    const {
        captcha: { isShown: isCaptchaShown },
        accessToken,
    } = useSelector((state: RootState) => state.auth);
    const { isMessageSended } = useSelector((state: RootState) => state.chat);
    const navigate = useNavigate();

    const [photoName, setPhotoName] = useState('');

    const handleChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
        const files = e.currentTarget.files;
        if (!files?.length) return;
        dispatch(requestUploadPhoto(files[0]));
        e.currentTarget.value = '';
    };

    const handleSubmit = (e: React.ChangeEvent<HTMLFormElement>) => {
        e.preventDefault();
        e.currentTarget.fileUpload.click();
    };

    useEffect(() => {
        if (!isMessageSended) return;
        navigate(-1);
    }, [dispatch, isMessageSended, navigate]);

    return (
        <>
            <Container sx={{ py: 2 }}>
                <Grid container spacing={3}>
                    {userPhotos.map(photo => (
                        <Grid
                            item
                            sm={3}
                            xs={6}
                            key={photo.id}
                            onClick={() => {
                                setPhotoName(photo.alt);
                                dispatch(
                                    sendMessage({
                                        type: 'photo',
                                        data: photo.alt,
                                        recipientId: interlocutorId,
                                    })
                                );
                            }}
                        >
                            <Box
                                sx={{
                                    cursor: 'pointer',
                                    position: 'relative',
                                    pb: '100%',
                                }}
                            >
                                <img
                                    src={IMG_DOMEN + photo.src}
                                    alt={photo.alt}
                                    loading="lazy"
                                    style={{
                                        position: 'absolute',
                                        top: 0,
                                        left: 0,
                                        objectFit: 'cover',
                                        width: '100%',
                                        height: '100%',
                                    }}
                                />
                            </Box>
                        </Grid>
                    ))}
                </Grid>
                <Box component="form" onSubmit={handleSubmit} sx={{ mt: 2 }}>
                    <input
                        type="file"
                        hidden
                        name="fileUpload"
                        multiple={false}
                        onChange={handleChange}
                    />

                    <Button type="submit" variant="contained">
                        Add photo
                    </Button>
                </Box>
            </Container>
            {isCaptchaShown && (
                <Captcha
                    onsubmit={(code: number) => {
                        dispatch(
                            sendMessage({
                                type: 'photo',
                                data: photoName,
                                recipientId: interlocutorId,
                                code,
                            })
                        );
                    }}
                />
            )}
        </>
    );
};
