import { getChats } from './../api/chats';
import { IChat } from '../meets/meetsSlice';
import { RootState } from '../../app/store';
import { call, put, select, take, takeLatest } from 'redux-saga/effects';
import { requestChats, setChats } from './chatsSlice';
import { requestAccessToken, setAccessToken } from '../auth/authSlice';

const processChats = (data: IChat[], chatList: IChat[]) => {
    const processedData: IChat[] = [];
    for (const newChatData of data.filter(({ dialog }) => dialog)) {
        let isNewChat = true;
        for (const oldChatData of chatList) {
            if (newChatData.id !== oldChatData.id) continue;
            isNewChat = false;
            if (newChatData.dialog.message === oldChatData.dialog.message)
                processedData.push(oldChatData);
            else
                processedData.push({
                    ...newChatData,
                    dialog: {
                        ...newChatData.dialog,
                        readed: false,
                    },
                });
        }
        if (isNewChat)
            processedData.push({
                ...newChatData,
                dialog: {
                    ...newChatData.dialog,
                    readed: false,
                },
            });
    }

    const result = processedData.map<IChat>(chat => {
        return {
            ...chat,
            human: {
                ...chat.human,
                name: chat.human.name ?? 'Unknown',
                age: chat.human.age ?? 'unknown',
                city: chat.human.city ?? 'unknown',
            },
            dialog: {
                ...chat.dialog,
                readed: chat.dialog.readed,
            },
        };
    });
    localStorage.setItem('chats', JSON.stringify(result));

    return result;
};

function* getChatsWorker(): Generator<any, any, any> {
    try {
        const { accessToken, userId } = yield select(
            (state: RootState) => state.auth
        );
        const { chats } = yield select((state: RootState) => state.chats);
        const res = yield call(getChats, userId, accessToken);
        yield put(setChats(processChats(res.data, chats)));
    } catch (error: any) {
        if (error.response && error.response.status === 401) {
            yield put(requestAccessToken());
            yield take(setAccessToken.type);
            yield put(requestChats());
        } else console.log(error);
    }
}

export function* chatsWatcher() {
    yield takeLatest(requestChats.type, getChatsWorker);
}
