import {
    Box,
    Button,
    Container,
    List,
    ListItem,
    ListItemText,
    TextField,
} from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { Captcha } from '../components/Captcha';
import {
    requestMessages,
    resetIsMessageSended,
    sendMessage,
} from '../features/chat/chatSlice';
import { useEffect } from 'react';
import { Navigate, Outlet, useNavigate, useParams } from 'react-router-dom';
import { RootState } from '../app/store';
import htmlParser from 'html-react-parser';
import { IMG_DOMEN } from '../app/config';
import {
    Send as SendIcon,
    CameraAlt as CameraAltIcon,
} from '@mui/icons-material';
import { useState } from 'react';

export const ChatPage = () => {
    const params = useParams();
    const interlocutorId = +(params.interlocutorId ?? '');
    const dispatch = useDispatch();
    const { messages } = useSelector((state: RootState) => state.chat);
    const {
        captcha: { isShown: isCaptchaShown },
    } = useSelector((state: RootState) => state.auth);
    const { isMessageSended } = useSelector((state: RootState) => state.chat);
    const navigate = useNavigate();

    const [message, setMessage] = useState('');

    const handleTextFieldChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setMessage(e.currentTarget.value);
    };

    const handleSubmit = (e: React.ChangeEvent<HTMLFormElement>) => {
        e.preventDefault();
        if (message.length)
            dispatch(
                sendMessage({
                    type: 'text',
                    data: message,
                    recipientId: interlocutorId,
                })
            );
        else navigate('./sendPhoto');
    };

    useEffect(() => {
        dispatch(requestMessages(interlocutorId));

        const updateMessagesIntervalId = setInterval(() => {
            dispatch(requestMessages(interlocutorId));
        }, 20000);

        return () => {
            clearInterval(updateMessagesIntervalId);
        };
    }, [dispatch, interlocutorId]);

    useEffect(() => {
        if (!isMessageSended) return;
        setMessage('');
        dispatch(resetIsMessageSended());
    }, [dispatch, isMessageSended]);

    return (
        <>
            <Container>
                <List
                    sx={{ paddingTop: '20px', paddingBottom: '60px' }}
                    disablePadding
                >
                    {messages
                        .slice()
                        .reverse()
                        .map(message => {
                            const [date, time] = new Date(message.added)
                                .toLocaleString('ru')
                                .split(', ');
                            return (
                                <ListItem
                                    disableGutters
                                    key={message.id}
                                    sx={{
                                        py: '4px',
                                        ...(message.fromId !==
                                            interlocutorId && {
                                            justifyContent: 'flex-end',
                                            textAlign: 'end',
                                        }),
                                    }}
                                >
                                    <ListItemText
                                        primary={
                                            message.isImg ? (
                                                <img
                                                    src={
                                                        IMG_DOMEN + message.src
                                                    }
                                                    alt={message.message}
                                                    style={{ maxWidth: '100%' }}
                                                />
                                            ) : (
                                                htmlParser(message.message)
                                            )
                                        }
                                        secondary={`${time} ${date}`}
                                        sx={{
                                            maxWidth: '80%',
                                            borderRadius: '5px',
                                            border: '1px solid #dee2e6',
                                            backgroundColor: '#fff',
                                            margin: '0',
                                            padding: '5px',
                                            flexGrow: 0,
                                            wordWrap: 'break-word',
                                        }}
                                    />
                                </ListItem>
                            );
                        })}
                </List>
            </Container>
            <Box
                sx={{
                    backgroundColor: '#fff',
                    position: 'fixed',
                    bottom: 0,
                    left: 0,
                    width: '100%',
                }}
            >
                <Container sx={{ py: 1 }}>
                    <Box
                        display="flex"
                        alignItems="stretch"
                        component="form"
                        onSubmit={handleSubmit}
                    >
                        <TextField
                            label="Message"
                            variant="outlined"
                            size="small"
                            value={message}
                            multiline
                            onChange={handleTextFieldChange}
                            sx={{ flexGrow: 1 }}
                            autoComplete="off"
                        />
                        <Button type="submit">
                            {message.length ? <SendIcon /> : <CameraAltIcon />}
                        </Button>
                    </Box>
                </Container>
            </Box>
            {isCaptchaShown && (
                <Captcha
                    onsubmit={(code: number) => {
                        dispatch(
                            sendMessage({
                                type: 'text',
                                data: message,
                                recipientId: interlocutorId,
                                code,
                            })
                        );
                    }}
                />
            )}
        </>
    );
};
