export const UserHeadline = ({ name, age, city, className }: IUserHeadline) => {
    return (
        <Box
            sx={{
                textOverflow: 'ellipsis',
                overflow: 'hidden',
                whiteSpace: 'nowrap',
            }}
        >
            <Box component="span" mr={1}>
                {name}
            </Box>
            <Box component="span" color="error.main" mr={1}>
                {age}
            </Box>
            <Box component="span" color="primary.main">
                {city}
            </Box>
        </Box>
    );
};
