import { PROXY_HOST } from './../../app/config';
import axios from 'axios';

export const getMessages = (
    userId: number,
    interlocutorId: number,
    accessToken: string
) =>
    axios.get(
        `https://dialog.api.dc2.a4sex.net/api/v1/users/${userId}/dialog`,
        {
            params: {
                tid: interlocutorId,
            },
            headers: {
                Authorization: `Bearer ${accessToken}`,
            },
        }
    );

export const postMessage = (
    recipientId: number,
    message: string,
    captcha: { token: string; code?: string },
    refreshToken: string
) =>
    axios.post(
        PROXY_HOST + '/https://freedom-sex.com/mailer/post',
        {
            id: recipientId,
            mess: message,
            captcha,
        },
        {
            headers: {
                Authorization: `Bearer ${refreshToken}`,
                'X-Requested-With': 'XMLHttpRequest',
            },
        }
    );

export const postPhoto = (
    recipientId: number,
    photoName: string,
    captcha: { token: string; code?: string },
    refreshToken: string
) =>
    axios.post(
        PROXY_HOST + '/https://freedom-sex.com/mailer/post',
        {
            id: recipientId,
            photo: photoName,
            captcha,
        },
        {
            headers: {
                Authorization: `Bearer ${refreshToken}`,
                'X-Requested-With': 'XMLHttpRequest',
            },
        }
    );

export const uploadPhoto = (formData: FormData, accessToken: string) => {
    return axios.post('https://photo.api.dc2.a4sex.net/upload', formData, {
        headers: {
            Authorization: `Bearer ${accessToken}`,
            'Content-Type': 'multipart/form-data',
        },
    });
};

export const getImage = (alias: string, fromId: number, accessToken: string) =>
    axios.get(`https://photo.api.dc2.a4sex.net/sends/${alias}`, {
        params: {
            tid: fromId,
        },
        headers: {
            Authorization: `Bearer ${accessToken}`,
        },
    });

export const getUserPhotos = (accessToken: string) =>
    axios.get(`https://photo.api.dc2.a4sex.net/photos`, {
        headers: {
            Authorization: `Bearer ${accessToken}`,
        },
    });
