import axios from 'axios';

interface IRequestLoginArgs {
    userId: number;
    password: string;
    code?: string;
    captchaToken?: string;
}

export const getAccessToken = (refreshToken: string) =>
    axios.post('https://auth.api.dc2.a4sex.net/api/v1/refresh', {
        token: refreshToken,
    });

export const getCapthcaId = (accessToken: string) =>
    axios.get('https://verify.api.dc2.a4sex.net/api/time', {
        headers: {
            Authorization: `Bearer ${accessToken}`,
        },
    });

export const getCapthcaToken = (capthcaId: string) =>
    axios.post('https://verify.api.dc2.a4sex.net/api/token', {
        id: capthcaId,
    });

export const getCapthcaCode = (captchaToken: string) =>
    axios.get(`https://verify.api.dc2.a4sex.net/api/image/${captchaToken}`);

export const requestVisit = (accessToken: string) =>
    axios.post(
        'https://visit.api.dc2.a4sex.net/api/visit',
        {},
        {
            headers: {
                Authorization: `Bearer ${accessToken}`,
            },
        }
    );
export const postLogin = ({
    userId,
    password,
    captchaToken,
    code,
}: IRequestLoginArgs) =>
    axios.post('https://auth.api.dc2.a4sex.net/api/v1/login', {
        login: userId,
        password,
        code,
        token: captchaToken,
    });
