import { IMG_DOMEN } from './../../app/config';
import { requestVisit } from './../api/auth';
import {
    postMessage,
    getMessages,
    getImage,
    getUserPhotos,
    postPhoto,
    uploadPhoto,
} from './../api/chat';
import { RootState } from './../../app/store';
import {
    call,
    select,
    takeLeading,
    put,
    takeLatest,
    take,
} from 'redux-saga/effects';
import {
    sendMessage,
    setMessages,
    requestMessages,
    addMessage,
    IMessage,
    IImage,
    sendMessageSuccess,
    requestUserPhotos,
    setUserPhotos,
    requestUploadPhoto,
} from './chatSlice';
import {
    requestAccessToken,
    requestCaptcha,
    hideCaptcha,
    setAccessToken,
} from '../auth/authSlice';

interface ISendMessageWorker {
    type: string;
    payload: {
        type: 'photo' | 'text';
        data: string;
        recipientId: number;
        code?: string;
    };
}

interface IGetMessagesWorkerArgs {
    type: string;
    payload: number;
}

function* sendMessageWorker({
    payload,
}: ISendMessageWorker): Generator<any, any, any> {
    try {
        const { refreshToken, accessToken, captcha } = yield select(
            (state: RootState) => state.auth
        );

        yield call(requestVisit, accessToken);

        const res = yield call(
            payload.type === 'text' ? postMessage : postPhoto,
            payload.recipientId,
            payload.data,
            { code: payload.code, token: captcha.token },
            refreshToken
        );

        if (res.data.error && res.data.error === 'need_captcha') {
            yield put(requestCaptcha());
        } else {
            yield put(
                addMessage({
                    ...res.data.message,
                    message:
                        payload.type === 'text'
                            ? res.data.message.mess
                            : `[[IMG:${payload.data}]]`,
                    added: res.data.message.date,
                })
            );
            yield put(sendMessageSuccess());
            yield put(hideCaptcha());
        }
    } catch (error) {
        console.log(error);
    }
}

function* getMessagesWorker({
    payload,
}: IGetMessagesWorkerArgs): Generator<any, any, any> {
    try {
        const { accessToken, userId } = yield select(
            (state: RootState) => state.auth
        );
        const res = yield call(getMessages, userId, +payload, accessToken);
        const messages: IMessage[] = [];
        // const images: IImage[] = [];

        for (const message of res.data) {
            // if (lastMessageId && message.id === lastMessageId) break;
            const [prefix, alias] = message.message
                .replace(/^\[\[|\]\]$/g, '')
                .split(':');
            if (prefix !== 'IMG') {
                message.isImg = false;
                messages.push({ ...message, isImg: false });
                continue;
            }
            try {
                const res = yield call(
                    getImage,
                    alias,
                    message.fromId,
                    accessToken
                );

                const [photo, src]: [photo: IImage, src: string] = [
                    res.data.photo,
                    res.data.photo._links.origin.href,
                ];
                // images.push({
                //     id: photo.id as string,
                //     src,
                //     alt: alias,
                //     sender: message.fromId,
                // });
                messages.push({ ...message, isImg: true, src });
                // yield put(incGalleryCounter());
            } catch (error) {
                console.log(error);
                messages.push({
                    ...message,
                    isImg: false,
                    message: '[[IMAGE NOT FOUND]]',
                });
            }
        }
        yield put(setMessages(messages));
    } catch (error: any) {
        if (error.response && error.response.status === 401) {
            yield put(requestAccessToken());
            yield take(setAccessToken.type);
            yield put(requestMessages(payload));
        } else console.log(error);
    }
}

function* getUserPhotosWorker({
    payload,
}: IGetMessagesWorkerArgs): Generator<any, any, any> {
    try {
        const { accessToken } = yield select((state: RootState) => state.auth);
        const { data } = yield call(getUserPhotos, accessToken);
        const photos: IImage[] = (data.photos as any[]).map(photo => ({
            id: photo.id,
            alt: photo.alias,
            src: photo._links.origin.href,
            sender: 0,
        }));
        yield put(setUserPhotos(photos));
    } catch (error: any) {
        if (error.response && error.response.status === 401) {
            yield put(requestAccessToken());
            yield take(setAccessToken.type);
            yield put(requestMessages(payload));
        } else console.log(error);
    }
}

interface IUploadPhotoWorkerArgs {
    type: string;
    payload: File;
}

function* uploadPhotoWorker({ payload }: IUploadPhotoWorkerArgs) {
    try {
        const { accessToken } = yield select((state: RootState) => state.auth);
        const formData = new FormData();
        formData.append('file', payload);
        const { data } = yield call(uploadPhoto, formData, accessToken);
        console.log(data);
        if (data.message === 'Duplicate files') alert(data.message);
        yield put(requestUserPhotos());
    } catch (error: any) {
        if (error.response && error.response.status === 401) {
            yield put(requestAccessToken());
            yield take(setAccessToken.type);
            yield put(requestUploadPhoto(payload));
        } else console.log(error);
    }
}

export function* chatWatcher() {
    yield takeLatest(requestMessages.type, getMessagesWorker);
    yield takeLeading(sendMessage.type, sendMessageWorker);
    yield takeLeading(requestUserPhotos.type, getUserPhotosWorker);
    yield takeLeading(requestUploadPhoto.type, uploadPhotoWorker);
}
