import axios from 'axios';

export const getMeets = (userId: number, token: string) =>
    axios.get('https://dialog.api.dc2.a4sex.net/api/contact_initials', {
        params: {
            userId,
        },
        headers: {
            Authorization: `Bearer ${token}`,
        },
    });

export const readMeet = (chatId: number, token: string) =>
    axios.put(
        `https://dialog.api.dc2.a4sex.net/api/contact_initials/${chatId}`,
        { unread: false },
        {
            headers: {
                Authorization: `Bearer ${token}`,
            },
        }
    );
