import { all } from 'redux-saga/effects';
import { authWatcher } from '../features/auth/authSaga';
import { chatWatcher } from '../features/chat/chatSaga';
import { chatsWatcher } from '../features/chats/chatsSaga';
import { meetsWatcher } from '../features/meets/meetsSaga';

export function* rootSaga() {
    yield all([authWatcher(), meetsWatcher(), chatWatcher(), chatsWatcher()]);
}
