import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';

export interface IChat {
    id: number;
    human: {
        id: number;
        name: string;
        age: number | 'unknown';
        city: string;
        userpic: {
            source: string;
        } | null;
    };
    dialog: {
        message: string;
        readed: boolean;
    };
}

export interface IMeetState {
    meets: IChat[];
    selectedMeet?: IChat;
}

const initialState: IMeetState = {
    meets: [],
};

export const meetsSlice = createSlice({
    name: 'meets',
    initialState,
    reducers: {
        setMeets: (state, action: PayloadAction<IChat[]>) => {
            state.meets = action.payload;
        },
        selectMeet: (state, action: PayloadAction<number>) => {
            const idx = action.payload;
            state.selectedMeet = state.meets[idx];
            state.meets[idx].dialog.readed = true;
        },
        requestMeets: () => {},
    },
});

export const { setMeets, selectMeet, requestMeets } = meetsSlice.actions;
export const meetsReducer = meetsSlice.reducer;
