import {
    ListItem,
    ListItemAvatar,
    ListItemText,
    Avatar,
    Divider,
} from '@mui/material';
import { Box } from '@mui/system';
import htmlParser from 'html-react-parser';
import { Link } from 'react-router-dom';
import { USERPIC_DOMEN } from '../app/config';
import { IChat } from '../features/meets/meetsSlice';
import { UserHeadline } from './UserHeadline';

interface IChatProps extends IChat {
    onclick?: Function;
    to: string;
}

interface ILinkComponentProps {
    to: string;
}

export const Chat = ({ human, dialog, onclick, to }: IChatProps) => {
    const backgroundColor = `#${Math.floor(human.id / 100)}`;

    const handleClick = () => {
        if (onclick) onclick();
    };

    return (
        <>
            <ListItem
                alignItems="flex-start"
                sx={{
                    cursor: 'pointer',
                    color: 'inherit',
                    ...(dialog.readed && { opacity: '.5' }),
                }}
                disableGutters
                component={Link}
                onClick={handleClick}
                to={to}
            >
                <ListItemAvatar>
                    <Avatar
                        style={{
                            backgroundColor,
                        }}
                        alt={human.name ?? 'Unknown'}
                        src={
                            human.userpic
                                ? USERPIC_DOMEN + human.userpic.source
                                : 'none'
                        }
                    />
                </ListItemAvatar>
                <ListItemText
                    primary={
                        <UserHeadline
                            name={human.name ?? 'Unknown'}
                            age={human.age ?? 'unknown'}
                            city={human.city ?? 'unknown'}
                        />
                    }
                    secondary={
                        <Box
                            component="span"
                            sx={{
                                display: 'block',
                                overflow: 'hidden',
                                textOverflow: 'ellipsis',
                                whiteSpace: 'nowrap',
                            }}
                        >
                            {htmlParser(dialog.message)}
                        </Box>
                    }
                />
            </ListItem>
            <Divider variant="inset" component="li" />
        </>
    );
};
