import { Box, Container, TextField, Button, Stack } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { requestLogin } from '../features/auth/authSlice';
import { useState } from 'react';
import { RootState } from '../app/store';
import { Captcha } from '../components/Captcha';

export const AuthPage = () => {
    const dispatch = useDispatch();
    const [userId, setUserId] = useState(0);
    const [password, setPassword] = useState('');

    const {
        captcha: { isShown: isCaptchaShown },
    } = useSelector((state: RootState) => state.auth);

    return (
        <>
            <Container>
                <Box
                    component="form"
                    sx={{
                        height: '100vh',
                        textAlign: 'center',
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}
                >
                    <Box mb={1}>
                        <TextField
                            id="userId"
                            label="User id"
                            variant="standard"
                            type="number"
                            value={userId || ''}
                            onChange={e => {
                                setUserId(+e.currentTarget.value);
                            }}
                        />
                    </Box>
                    <Box mb={2}>
                        <TextField
                            id="password"
                            label="Password"
                            variant="standard"
                            value={password}
                            onChange={e => {
                                setPassword(e.currentTarget.value);
                            }}
                        />
                    </Box>
                    <Stack direction="row" spacing={2}>
                        <Button
                            variant="contained"
                            onClick={() => {
                                dispatch(requestLogin({ userId, password }));
                            }}
                        >
                            Login
                        </Button>
                        <Button variant="contained" disabled>
                            Register
                        </Button>
                    </Stack>
                </Box>
            </Container>
            {isCaptchaShown && (
                <Captcha
                    onsubmit={(code: number) => {
                        dispatch(requestLogin({ userId, password, code }));
                    }}
                />
            )}
        </>
    );
};
